@import "ScssHelpers/helpers-all.scss";

.text_section {
	// padding-top: 4.6rem;
	// padding-bottom: 7.6rem;
}

.text_section__title {
	font-weight: 400;

	@include bp($point_2, min) {
		max-width: 90%;
		font-size: 2.2rem;
		line-height: 1.45;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		line-height: 1.5;
		letter-spacing: -0.03em;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.text_section__description {
	@include text;
	font-weight: 400;

	&:not(:last-child) {
		margin-bottom: 3.6rem;
	}

	@include bp($point_2, min) {
		max-width: 92%;
		line-height: 1.5;
	}

	@include bp($point_3) {
		line-height: 1.45;
		letter-spacing: -0.012em;
		color: rgba(0, 0, 0, 0.8);
	}
}
