@import "ScssHelpers/helpers-all.scss";

.cooking_details {
	// padding-top: 8.8rem;
}

.cooking_details__row {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch);
	}
}

.cooking_details__main_col {
	@include bp($point_2, min) {
		width: 63.2%;
		padding-right: 3rem;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.cooking_details__side_col {
	@include bp($point_2, min) {
		width: 36.8%;
		padding-left: 5.6rem;
		margin-top: 0.8rem;
		border-left: 1px solid $beige_2;
	}

	@include bp($point_3) {
		padding-top: 2.8rem;
		border-top: 1px solid $beige_2;
	}
}

.cooking_details__main_list {
	@include bp($point_2, min) {
		max-width: 70rem;
	}
}

.cooking_details__main_item {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.cooking_details__main_title {
	@include h3;
	line-height: 1.05;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: -0.03em;
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.cooking_details__main_text {
	@include text;
	@include bp($point_2, min) {
		line-height: 1.5;
	}

	@include bp($point_3) {
		line-height: 1.42;
		letter-spacing: -0.015em;
	}
}

.cooking_details__side_head {
	@include h4;
	line-height: 1.14;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	@include bp($point_3) {
		letter-spacing: -0.02em;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}


.cooking_details__side_item {
	@include flex_block(row, nowrap, flex-start, flex-start);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.cooking_details__side_decor {
	flex-shrink: 0;
	border-radius: 50%;
	border: 1px solid $gold_2;

	@include bp($point_2, min) {
		width: 7.2rem;
		height: 7.2rem;
	}

	@include bp($point_3) {
		width: 5.6rem;
		height: 5.6rem;
	}
}

.cooking_details__side_content {
	@include bp($point_2, min) {
		padding-top: .6rem;
		padding-left: 3rem;
	}

	@include bp($point_3) {
		padding-top: .4rem;
		padding-left: 1.4rem;
	}
}

.cooking_details__side_title {
	@include h5;
	text-transform: uppercase;
	line-height: 1;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: .5rem;
		}
	}
}

.cooking_details__side_text {
	font-weight: 400;

	@include bp($point_2, min) {
		font-size: 1.4rem;
		line-height: 1.57;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.38;
		letter-spacing: -.02em;
	}
}
