@import "ScssHelpers/helpers-all.scss";

.roasting_times {
	// padding-top: 6rem;
	// padding-bottom: 11.8rem;
	background-color: $gray_3;
}

.roasting_times__title {
	@include h2;
	line-height: 1;
	text-align: center;
	letter-spacing: -0.018em;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: .8rem;
		}
	}
}

.roasting_times__description {
	@include text;

	max-width: 80.6rem;
	width: 100%;
	margin: 0 auto;

	font-weight: 400;
	line-height: 1.5;
	text-align: center;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.roasting_times__wrap {
	position: relative;
	background-color: $white;

	@include bp($point_2, min) {
		padding: 2.6rem 5.6rem 5.8rem;
	}

	@include bp($point_3) {
		margin-right: calc(var(--gl-indent)* -1);
		padding: 2.2rem 2.4rem 2.8rem;
	}

	&:before,
	&:after {
		position: absolute;
		top: 0;
		right: 0;

		display: block;
		height: 100%;


		content: '';
	}

	&:after {
		z-index: 9;
		width: 7.8rem;
		background: linear-gradient(270deg, $white 0%, rgba(255,255,255,0) 100%);
	}

	&:before {
		z-index: 0;
		width: var(--gl-indent);
		background: $white;
	}
}

.roasting_times__content {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, stretch, flex-start);
	}

	&.swiper-wrapper {
		align-items: stretch;
	}
}

.roasting_times__content_row {
	//padding: 0 1rem;

	&.roasting_times__content_row--title_mod {

		border-bottom: 1px solid $gray_3;
		font-weight: 500;

		@include bp($point_2, min) {
			flex: auto;
			padding-bottom: 2.2rem;
			margin-bottom: 3rem;
			font-size: 1.8rem;
			line-height: 1.78;
		}

		@include bp($point_3) {
			//padding-bottom: 1.2rem;
			margin-bottom: 1rem;
			height: 3.2rem;
			overflow: hidden;
			line-height: 1;
		}
	}

	&.roasting_times__content_row--descrip_mod {
		font-weight: 400;
		line-height: 1.5;
		flex: auto;

		@include bp($point_2, min) {
			font-size: 1.6rem;

			&:not(:last-child) {
				margin-bottom: 3.2rem;
			}
		}

		@include bp($point_3) {
			&:not(:last-child) {
				margin-bottom: .7rem;
			}
		}
	}

	@include bp($point_3) {
		font-size: 1.4rem;
		padding-right: 1rem;
	}
}

.roasting_times__content_col {
	@include bp($point_2, min) {
		&:first-child {
			width: 25.8%;
		}

		&:nth-child(2) {
			width: 44.5%;
		}

		&:nth-child(3) {
			width: 29.7%;
		}
	}

	&.swiper-slide {
		height: auto;
	}
}

.roasting_times__content_col_in {
	display: flex;
	flex-direction: column;
	height: 100%;
}

.roasting_times__container {
	position: relative;
}

.roasting_times__button {
	z-index: 10;
	position: absolute;
	top: 50%;
	margin-top: -2.2rem;

	@include bp($point_2, min) {
		display: none;
	}

	&.roasting_times__button--prev_mod {
		left: -2.2rem;
	}

	&.roasting_times__button--next_mod {
		right: -2.2rem;
	}
}

